import React, { useState, useRef, useEffect } from "react";
import wrapper from "../../commons/WebComponentWrapper";
import { FormGroup, Accordion, AccordionItem } from "carbon-components-react";

const dispatchedEvents = [];

/*
    Example: 
        <srs-form sections=[{name: "section1", title: "Section 1"}, {name: "section2", title: "Section 2"}]>
            <srs-form-section name="section1">
                ...
            </srs-form-section>
            <srs-form-section name="section2">
                ...
            </srs-form-section>
        </srs-form>
*/
const SrsFormSection = function (props, ref) {
    const [isExpanded, setIsExpanded] = useState(props.open);

    const handleToggle = () => setIsExpanded(!isExpanded);
    const slotRef = useRef(null);
    useEffect(() => {
        if (slotRef.current) {
            let assignedElements = slotRef.current.assignedElements();
            for (let i = 0; i < assignedElements.length; i++) {
                assignedElements[i].form_data = JSON.stringify(props.form_data);
            }
        }
        // setIsExpanded(props.open);

    }, [props.title, props.form_data, props.open]);
    return (
        <AccordionItem title={props.title} onHeadingClick={handleToggle} open={isExpanded}>
            <FormGroup>
                <slot ref={slotRef} style={{ display: "flex", flexDirection: "row", flexWrap: "wrap", flex: "1 1 100%", margin: "16px 8px" }}></slot>
            </FormGroup>
        </AccordionItem>
    );
}

const styles = `
    .cds--accordion__content {
        padding-left: 1rem !important;
        padding-right: 1rem !important;
    }
`
const props = [{
    name: "open",
    defaultValue: true,
    type: "boolean"
}, {
    name: "form_data",
    defaultValue: {},
    type: "object"
}, {
    name: "title",
    defaultValue: "",
    type: "string"
}]

const slots = [{name: "default", allowed: ["srs-form-field"]}];

const WrappedComponent = wrapper(React.forwardRef(SrsFormSection), props);
export { WrappedComponent, styles, props, dispatchedEvents, slots };